.contactForm {
  font-size: 24px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  width: 450px;
  padding: 10px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  border-radius: 2px;
  margin-bottom: 20px;
}

.contactFormItem:not(:last-child) {
  margin-bottom: 10px;
}

.input {
  font-size: 20px;
  height: 40px;
  border-color: #6b6e72;
}

.addBtn {
  background-color: #4267b2;
  padding: 8px 16px;
  border-radius: 3px;
  transition: all 200ms ease;
  font-weight: 500;
  text-align: center;
  display: inline-block;
  color: #fff;
  border: 0;
  text-decoration: none;
  cursor: pointer;
  font-family: inherit;
  font-size: 16px;
  line-height: 24px;
  font-style: normal;
  min-width: 180px;
}

.addBtn:hover {
  background: #375ba1;
}